import { useState, useEffect } from 'react'

import golapi from "./images/golapi.webp"

function Loading({ loading }) {

  const [wrappperStyle, setWrappperStyle] = useState({})
  const [logoStyle, setLogoStyle] = useState({})

  useEffect(() => {
    if (loading === true) {
      setWrappperStyle({
        "display": "flex"
      })
      setTimeout(() => {
        setWrappperStyle({
          "display": "flex",
          "opacity": "1"
        })
        setLogoStyle({
          "transform": "scale(1)"
        })
      }, 1)
    }
    else {
      setWrappperStyle({
        "opacity": "0"
      })
      setLogoStyle({
        "transform": "scale(0)"
      })
      setTimeout(() => {
        setWrappperStyle({
          "display": "none",
          "opacity": "0"
        })
      }, 500)
    }
  }, [loading])

  return (
    <div className="loading" style={wrappperStyle}>
      <img src={golapi} alt="Golapi Brothers' Logo" style={logoStyle} />
    </div>
  )
}

export default Loading
