function Navbar({ style, category_titles, categoryIndex, handleCategorySelection }) {
  return (
    <div style={style} className="nav">
      {category_titles.map((category_title, index) => (
        <p
          key={index}
          onClick={() => handleCategorySelection(index)}
          style={categoryIndex === index ? { "fontWeight": "bold" } : {}}>
          {category_title}
        </p>
      ))}
    </div>
  )
}

export default Navbar
