import { Swiper, SwiperSlide } from "swiper/react";

import logo1 from "./images/1.webp"
import logo2 from "./images/2.webp"
import logo3 from "./images/3.webp"

function LogoSection() {
  return (
    <Swiper className="logo-section"
      slidesPerView={3}>
      <SwiperSlide><img alt="logo" src={logo1} /></SwiperSlide>
      <SwiperSlide><img alt="logo" src={logo2} /></SwiperSlide>
      <SwiperSlide><img alt="logo" src={logo3} /></SwiperSlide>
    </Swiper>
  )
}

export default LogoSection
